<template>
  <div class='hundred'>
    <div class='allFlex'>
      <router-link tag="div" :to="{name:'collection'}" class='half textCenter homeTopm'>
        <img src='../assets/doshou.png' />
        <p>快捷收款</p>
      </router-link>
      <router-link tag="div" :to="{name:'repayment'}" class='half textCenter homeTopm'>
        <img src='../assets/znhk.png' />
        <p>智能还款</p>
      </router-link>
      <router-link tag="div" :to="{name:'emptyrepayment'}" class='half textCenter homeTopm'>
        <img src='../assets/kkhk.png' />
        <p>空卡还款</p>
      </router-link>
    </div>
    <div class='homeLogo'>
      <!--<router-link :to="{name: 'share'}" tag="a"><img src='../assets/code.png' class='shareCode' /></router-link>-->
      <el-carousel height="5rem">
        <el-carousel-item v-for='item in bannerList' :key='item.id'>
          <img :src="item.singleNo? item.singleNo : require('../assets/banner.png')" class='imgBanner' />
        </el-carousel-item>
      </el-carousel>
    </div>
    <div class='homeIcon'>
    </div>
    <div class='allFlex homeList'>
      <router-link tag="div" :to="{name:'repayment'}" class='textCenter'>
        <img src='../assets/card.png'  class='imageOne' />
        <p>我的信用卡</p>
      </router-link>
      <div class='textCenter'>
        <router-link tag="div" :to="{name:'intermediaryagency'}" class='textCenter'>
          <img src='../assets/surrender.png'  class='imageOne' />
          <p>中介代还</p>
        </router-link>
      </div>
      <router-link :to="{name: 'share'}" tag='div' class='textCenter'>
        <img src='../assets/share.png'  class='imageOne' />
        <p>邀请分享</p>
      </router-link>
      <router-link :to="{name: 'upgrade'}" tag='div' class='textCenter'>
        <img src='../assets/upgrade.png'  class='imageOne' />
        <p>会员升级</p>
      </router-link>
      <div class='textCenter' @click="card(1, '信用卡办理')">
        <img src='../assets/handle.png'  class='imageOne' />
        <p>信用卡办理</p>
      </div>
      <div class='textCenter' @click="card(2, '积分兑换')">
        <img src='../assets/exchange.png'  class='imageOne' />
        <p>积分兑换</p>
      </div>
      <div class='textCenter' @click="card(2, '卡测评')">
        <img src='../assets/evaluation.png'  class='imageOne' />
        <p>卡测评</p>
      </div>
      <div class='textCenter' @click="card(2, '征信查询')">
        <img src='../assets/credit.png'  class='imageOne' />
        <p>征信查询</p>
      </div>
    </div>
    <div class='homeTutorial'>
        <div>
           <div class='tutorial'>
            行业资讯
          </div>
          <router-link :to="{name: 'commerce'}" tag='div' class='tutorialImg'>
              <img src='../assets/bannerX.png'>
          </router-link>
        </div>
        <div>
           <div class='tutorial'>
            分享文案
          </div>
          <router-link :to="{name: 'cool'}" tag='div' class='tutorialImg'>
              <img src='../assets/bannerW.png'>
          </router-link>
        </div>
    </div>
    <div class='bottomLong'></div>
  </div>
</template>
<script>

export default {
  data () {
    return {
      agentNo: '',
      version: '',
      merchantNo: '',
      bannerList: [],
      cardUrl: ''
    }
  },
  components: {
  },
  watch: {
  },
  created () {
    this.version = this.$stact.state.version
    this.agentNo = this.$stact.state.agentNo
    this.merchantNo = JSON.parse(this.$stact.state.token)[0].merchantNo
    this.banner()
    console.log(this.$stact)
  },
  mounted () {
  },
  methods: {
    banner () {
      let vm = this
      let parmas = {
        '0': '0700',
        '3': '190108',
        '44': vm.agentNo,
        '59': vm.version
      }
      let url = vm.$utils.queryParams(vm.$mdata.mdGet(parmas))
      vm.$http.get(`request.app${url}`)
        .then(res => {
          if (res.data[39] === '00') {
            this.bannerList = JSON.parse(res.data[57])
          }
        })
        .catch(err => {
          console.log(err)
        })
    },
    card (type, title) {
      let vm = this
      if (type === 2) {
        vm.$message({
          message: '暂无开放',
          center: true,
          offset: 30,
          duration: 2500,
          type: 'success'
        })
        return
      }
      let parmas = {
        '0': '0700',
        '3': '890005',
        '42': vm.merchantNo,
        '43': type,
        '44': vm.agentNo,
        '59': vm.version
      }
      let url = vm.$utils.queryParams(vm.$mdata.mdGet(parmas))
      vm.$http.get(`request.app${url}`)
        .then(res => {
          if (res.data[39] === '00') {
            vm.$router.push({ name: 'ifarme', query: { url: res.data[38], title: title } })
          } else {
            vm.$message({
              message: res.data[39],
              center: true,
              offset: 30,
              duration: 2500,
              type: 'success'
            })
          }
        })
        .catch(err => {
          console.log(err)
        })
    }
  }
}
</script>
